import newsNavBar from "../../components/news/news-nav-bar.vue";
import commonList from "../../components/common/common-list.vue";
import loadMore from '../../components/common/load-more.vue';
import topicNav from "../../components/news/topic-nav.vue";
import topicList from "../../components/news/topic-list.vue"
export default {
    data() {
        return {
            tabIndex: 0,//当前选中的tab的索引值 默认为1
            tabBar: [
                {name: "关注",id: "follow"},
                {name: "话题",id: "topic"}
            ],
            topicBanner:{
                swiper:[],
                nav:[],
                list:[]
            },
            swiperHeight:500,
            followList:{
               loadText:"上拉加载更多",
                list:[
                    // 纯文字样式 textStyle
                    {
                        userPic:"../../static/demo/userpic/10.jpg",
                        userName:"ATM",
                        gender:0,//0:女 1:男
                        age:25,
                        isFollow:false,
                        title:"透明质酸钠",
                        textStyle:true,
                        PicTextStyle:false,
                        videoStyle:false,
                        shareStyle:false,
                        shareNum:3498,
                        commentNum:3456,
                        likeNum:345,
                        location:"PekingU",
                    },
                    //图文样式 PicTextStyle:true,
                    {
                        userPic:"../../static/demo/userpic/10.jpg",
                        userName:"31K473k",
                        gender:1,//0:女 1:男
                        age:25,
                        isFollow:false,
                        title:"...轻轻呼唤你的名字",
                        titlePic:"../../static/demo/datapic/13.jpg",
                        textStyle:false,
                        PicTextStyle:true,
                        videoStyle:false,
                        shareStyle:false,
                        location:"PekingU",
                        shareNum:3498,
                        commentNum:3456,
                        likeNum:345,
                    },
                    //视频样式
                    {
                        userPic:"../../static/demo/userpic/10.jpg",
                        userName:"momo",
                        gender:0,//0:女 1:男
                        age:25,
                        isFollow:false,
                        title:"欢乐恶搞不是法外之地",
                        titlePic:"../../static/demo/datapic/2.jpg",
                        textStyle:false,
                        PicTextStyle:false,
                        videoStyle:{
                            playNum:"34W",
                            length:"3:51"
                        },
                        shareStyle:false,
                        location:"PekingU",
                        shareNum:3498,
                        commentNum:3456,
                        likeNum:345,
                    },
                    //分享样式
                    {
                        userPic:"../../static/demo/userpic/10.jpg",
                        userName:"ATM",
                        gender:0,//0:女 1:男
                        age:25,
                        isFollow:false,
                        title:"透明质酸钠",
                        titlePic:"",
                        textStyle:false,
                        PicTextStyle:false,
                        videoStyle:false,
                        shareStyle:{
                            shareTitle:"长笛",
                            sharePic:"../../static/demo/datapic/2.jpg"
                        },
                        location:"PekingU",
                        shareNum:3498,
                        commentNum:3456,
                        likeNum:345,
                    }
                ]
            }              
        };
    },
    methods: {
        __init(){
            this.getSwiper();
            this.getHot();
            this.getNav();
        },
        // 获取广告
        async getSwiper(){
            let [err,res] = await this.$http.get('adsense/0');
            if (!this.$http.errorCheck(err,res)) return;
            let arr = [];
            let list = res.data.data.list;
            for (let i = 0; i < list.length; i++) {
                arr.push({
                    src:list[i].src,
                    url:list[i].url
                })
            }
            this.topicBanner.swiper = arr;
        },
        // 获取热门分类
        async getNav(){
            let [err,res] = await this.$http.get('topicclass');
            if (!this.$http.errorCheck(err,res)) return;
            let arr = [];
            let list = res.data.data.list;
            for (let i = 0; i < list.length; i++) {
                arr.push({
                    id:list[i].id,
                    name:list[i].classname
                })
            }
            this.topicBanner.nav = arr;
        },
        // 获取热门话题
        async getHot(){
            let [err,res] = await this.$http.get('hottopic');
            if (!this.$http.errorCheck(err,res)) return;
            let arr = [];
            let list = res.data.data.list;
            for (let i = 0; i < list.length; i++) {
                arr.push({
                    id:list[i].id,
                    titlePic:list[i].titlepic,
                    title:list[i].title,
                    desc:list[i].desc,
                    totalPostNum:list[i].post_count,
                    todayPostNum:list[i].todaypost_count,
                })
            }
            this.topicBanner.list = arr;
        },
        //点击切换
        changeTab(index) {
            this.tabIndex = index;
        },
        //tabBar滑动事件
        tabChange(e){
            this.tabIndex = e.detail.current;
        },
        //上拉加载
        loadMore(){
            if( this.followList.loadText != "上拉加载更多"){
                return;//如果正在加载中(＾o＾)ﾉ或没有数据可以加载，则停止请求
            }
            this.followList.loadText = "加载中(＾o＾)ﾉ";
            //修改状态
            setTimeout(()=> {                  
                //示例:加载2000ms后从服务端获取了新的数据
                let obj = {                       
                    //视频样式          
                        userPic:"../../static/demo/userpic/10.jpg",
                        userName:"ATM",
                        gender:"0",//0:女 1:男
                        age:25,
                        isFollow:false,
                        title:"透明质酸钠",
                        titlePic:"../../static/demo/datapic/2.jpg",
                        textStyle:false,
                        PicTextStyle:false,
                        videoStyle:{
                            playNum:"34W",
                            length:"3:51"
                        },
                        shareStyle:false,
                        location:"PekingU",
                        shareNum:3498,
                        commentNum:3456,
                        likeNum:345,
                    
                };
                this.followList.list.push(obj);//追加
                
                this.followList.loadText = "上拉加载更多";     //复原状态              
            }, 2000);
            
            //this.followList.loadText = "没有更多数据";
            
        },
        // 打开搜索页
        openSearch(){
            uni.navigateTo({
                url:'../search/search?searchType=topic'
            });
        }
    },
    components: {
        commonList,
        newsNavBar,
        loadMore,
        topicNav,
        topicList
    },
    onLoad() {
        uni.getSystemInfo({
            success: (res) => {
                let height= res.windowHeight - uni.upx2px(100);//tabBar的高度是100upx
                this.swiperHeight = height;
            }
        })
        this.__init();
    },
}
